/*_________________________________________*/
/*--------------Verify Email------------- */
.verify {
	margin-top       : 20px;
	color            : black;
	padding          : 20px;
	box-shadow       : 0px 1px 8px rgb(2, 1, 1);
	background-color : $fColorBright;

	.row div .card .card-header {
		margin-bottom: 10px;
	}
}
//--Errors----
div.alert {
	max-width : 600px;
	width     : 70vw;
	padding   : 3px;
	margin    : 1px auto 5px auto;
}
//--recapcha------
div.g-recaptcha {
	transform        : scale(0.8);
	transform-origin : 0 0;
	margin           : 0 auto;
	width            : 250px;
	display          : block;
}
